import { useEffect, useState } from 'react'
import { HashRouter as Router, Route } from 'react-router-dom'
import AboutPage from './pages/about/AboutPage';
import ContactPage from './pages/contact/ContactPage';
import FellowshipPage from './pages/fellowship/FellowshipPage';
import HomePage from './pages/home/HomePage';
import ProjectsPage from './pages/projects/ProjectsPage';
import ResourcesPage from './pages/resources/ResourcesPage';
import JoinModal from './shared-components/JoinModal';
import Navbar from './shared-components/Navbar';
import Loading from './shared-components/Loading';

import './css/typography.css'
import './css/utilities.css'


function App() {

  window.addEventListener("hashchange", () => {
    window.scrollTo({top: 0})
  })

  let [loading, setLoading] = useState(true);
  let [joinContent, setJoinContent] = useState({});

  useEffect(() => {
    fetch('https://ndohertydesign-api.herokuapp.com/notion-content/nuea/join')
    .then(response => response.json())
    .then((joinContent) => {
      setJoinContent(joinContent)
      setLoading(false)
    })

    return () => {}
  }, [])

  if (loading) {
    return  <div style={{width: "100vw", height: "100vh"}}>
              <Loading />
            </div>
  }

  return (
    <div>
    <Router>
        <Navbar />
        <JoinModal content={joinContent} />
        <Route path="/" exact component={HomePage} />
        <Route path="/about" exact component={AboutPage} />
        <Route path="/fellowship" exact component={FellowshipPage} />
        <Route path="/resources" exact component={ResourcesPage} />
        <Route path="/projects" exact component={ProjectsPage} />
        <Route path="/contact" exact component={ContactPage} />
      </Router>
    </div>
   
  );
}

export default App;
