import React, { Component } from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import Loading from '../../shared-components/Loading';
import MemberCard from './MemberCard';

import '../../css/about.css'


export default class AboutPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: true,
      content: null
    }
  }

  componentDidMount() {
    fetch('https://ndohertydesign-api.herokuapp.com/notion-content/nuea/about')
    .then(response => response.json())
    .then((aboutContent) => {
      this.setState({content: aboutContent, loading: false})
    })
  }

  render() {
    if (this.state.loading) {
      return <div style={{width: "100vw", height: "100vh"}}>
               <Loading />
             </div> 
    }


    return (
      <div>
        <Container fluid>
          <Row className="section">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["About-Header"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["About-Description"]}</p>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
        <Container fluid className="bg-color-red">
          <Row className="section text-white">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header mb-4">{this.state.content["Members-Header"]}</h3>
              <div className="w-100 d-flex flex-wrap">
                {this.state.content['Members'].map((member) => 
                  <MemberCard member={member} />
                )}
              </div>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
      </div>
    )
  }
}
