import React, { Component } from 'react'
import { Button, Image } from 'react-bootstrap'
import { Link } from 'react-router-dom'
import { Media } from 'react-breakpoints'
import $ from 'jquery'

import '../css/navbar.css'

export default class Navbar extends Component {

  closeOverlay() {
    $('#navigation-overlay').css('width', "0")
  }

  render() {
    return (
      <Media>
        {({ breakpoints, currentBreakpoint }) =>
          breakpoints[currentBreakpoint] >= breakpoints.tablet ? (
            <div style={{height: "65px"}}>
              <div id="navbar" className="mrs-eaves bold">
                <Link id="home-link" to="/">
                  <div className="d-flex my-2 color-red align-items-center">
                    <Image src="/neuEA.png" className="navbar-logo"></Image>
                      <h3 id="nuEA-title" className="m-0">NORTHEASTERN UNIVERSITY<br />EFFECTIVE ALTRUISM</h3>       
                  </div>
                </Link>
                
      
                <span>
                  <Link to="/about">About</Link>
                  <Link to="/resources">Resources</Link>
                  <Link to="/fellowship">Fellowship</Link>
                  <Link to="/projects">Projects</Link>
                  <Link to="/contact">Contact</Link>
                  <Button variant="dark" onClick={() => $('#join-modal-overlay').toggleClass('d-none') }>Join</Button>
                </span>
              
              </div>
            </div>
          ) : (
            <div style={{height: "65px"}}>
              <div id="navbar" className="mrs-eaves bold">
                <Link id="home-link" to="/">
                  <div className="d-flex my-2 color-red align-items-center">
                    <Image src="/neuEA.png" className="navbar-logo"></Image>
                    <h3 id="nuEA-title" className="m-0 mobile-size">
                      NORTHEASTERN UNIVERSITY<br />
                      EFFECTIVE ALTRUISM
                    </h3>       
                  </div>
                </Link>
                
      
                <span>
                  <Image className="menu-button" src="/icons/menu-24px.svg" onClick={() => $('#navigation-overlay').css('width', "75vw")} />
                </span>
              </div>

              <div id="navigation-overlay" className="bg-color-red mrs-eaves bold">
                <span id="navigation-overlay-exit" onClick={() => $('#navigation-overlay').css('width', "0")}>X</span>
                <div>
                  <Link onClick={this.closeOverlay} to="/about"><p>About</p></Link>
                  <Link onClick={this.closeOverlay} to="/resources"><p>Resources</p></Link>
                  <Link onClick={this.closeOverlay} to="/fellowship"><p>Fellowship</p></Link>
                  <Link onClick={this.closeOverlay} to="/projects"><p>Projects</p></Link>
                  <Link onClick={this.closeOverlay} to="/contact"><p>Contact</p></Link>
                </div>
              </div>

              <div id="join-button-footer">
                <Button className="uppercase mrs-eaves bold" variant="dark" onClick={() => $('#join-overlay').css('width', '75vw') }>Join</Button>
              </div>
            </div>
          )
        }
      </Media>
    )
  }
}
