import React, { Component } from 'react'
import { Container, Row, Col, Image } from 'react-bootstrap'
import Loading from '../../shared-components/Loading';

import '../../css/projects.css'

export default class ProjectsPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: true,
      content: null
    }
  }

  componentDidMount() {
    fetch('https://ndohertydesign-api.herokuapp.com/notion-content/nuea/projects')
    .then(response => response.json())
    .then((projectsContent) => {
      this.setState({content: projectsContent, loading: false})
    })
  }

  render() {
    if (this.state.loading) {
      return <div style={{width: "100vw", height: "100vh"}}>
               <Loading />
             </div> 
    }


    return (
      <div>
        <Container fluid>
          <Row className="section">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["Projects-Header"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["Projects-Description"]}</p>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
        <Container fluid className="bg-color-red">
          <Row className="section text-white">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              {this.state.content['Projects'].map((project) => {
                return (
                  <div className="py-4">
                    {project.image[0] && <Image src={project.image[0]} className="img-fluid w-50" />}
                    <h3 className="mrs-eaves bold header">{project.title}</h3>
                    {project.timeframe && 
                      <p className="mr-eaves italic">
                        {new Date(Date.parse(project.timeframe.start)).toLocaleDateString()}
                        {project.timeframe.end && ` â€” ${new Date(Date.parse(project.timeframe.end)).toLocaleDateString()}`}
                      </p>
                    }
                    <p className="mr-eaves paragraph">{project.description}</p>
                  </div>
                )
              })}
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
      </div>
    )
  }
}