import React, { Component } from 'react'
import { Button } from 'react-bootstrap'
import { Media } from 'react-breakpoints'
import $ from 'jquery'

import "../css/join.css"


export default class JoinModal extends Component {
  render() {
    return (
      <Media>
        {({ breakpoints, currentBreakpoint }) =>
          breakpoints[currentBreakpoint] >= breakpoints.tablet ? (
            <div id="join-modal-overlay" className="d-none">
              <div id="join-modal">
                <div className="w-100 d-flex justify-content-end">
                  <span className="mrs-eaves pointer" onClick={() => $('#join-modal-overlay').toggleClass('d-none')}>X</span>
                </div>
                
                <h3 className="mrs-eaves bold header mb-5 mt-3 color-red px-3">
                  {this.props.content['Join-Section-Header']}
                </h3>

                <div className="d-flex justify-content-space-between mt-2">
                  <div className="text-left w-50 px-3">
                    <h3 className="mrs-eaves bold header small color-red"> 
                      {this.props.content['Join-Option-1-Header']}
                    </h3>
                    <p className="mr-eaves paragraph">
                      {this.props.content['Join-Option-1-Desc']}
                    </p>
                    <a target="_blank" href={this.props.content['Join-Option-1-Button-Link']}><Button className="mb-5 uppercase" variant="outline-dark">{this.props.content['Join-Option-1-Button-Text']}</Button></a> 
                  </div>

                  <div className="text-left w-50 px-3">
                  <h3 className="mrs-eaves bold header small color-red"> 
                      {this.props.content['Join-Option-2-Header']}
                    </h3>
                    <p className="mr-eaves paragraph">
                      {this.props.content['Join-Option-2-Desc']}
                    </p>
                    <a target="_blank" href={this.props.content['Join-Option-2-Button-Link']}><Button className="mb-5 uppercase" variant="outline-dark">{this.props.content['Join-Option-2-Button-Text']}</Button></a> 
                  </div>
                </div>
              
              </div>
            </div>
          ) : (
            <div>
              <div id="join-overlay" className="bg-color-red mrs-eaves bold">
                <span id="join-overlay-exit" onClick={() => $('#join-overlay').css('width', "0")}>X</span>
                <div>
                  <h3 className="mrs-eaves bold mobile-size header mb-5 small">
                    {this.props.content['Join-Section-Header']}
                  </h3>

                  <h3 className="mrs-eaves bold mobile-size header small"> 
                    {this.props.content['Join-Option-1-Header']}
                  </h3>
                  <p className="mr-eaves mobile-size">
                    {this.props.content['Join-Option-1-Desc']}
                  </p>
                  <a target="_blank" href={this.props.content['Join-Option-1-Button-Link']}><Button className="mb-5 uppercase" variant="outline-light">{this.props.content['Join-Option-1-Button-Text']}</Button></a> 

                  <h3 className="mrs-eaves bold mobile-size header small"> 
                    {this.props.content['Join-Option-2-Header']}
                  </h3>
                  <p className="mr-eaves mobile-size">
                    {this.props.content['Join-Option-2-Desc']}
                  </p>
                  <a target="_blank" href={this.props.content['Join-Option-2-Button-Link']}><Button className="uppercase" variant="outline-light">{this.props.content['Join-Option-2-Button-Text']}</Button></a> 
                  
                </div>
              </div>
            </div>
          )
        }
      </Media>
    )
  }
}