import React, { Component } from 'react'
import { Container, Row, Col, Button, Image } from 'react-bootstrap'
import Loading from '../../shared-components/Loading';
import $ from 'jquery'

import '../../css/fellowship.css'

export default class FellowshipPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: true,
      content: null
    }
  }

  componentDidMount() {
    fetch('https://ndohertydesign-api.herokuapp.com/notion-content/nuea/fellowship')
    .then(response => response.json())
    .then((fellowshipContent) => {
      this.setState({content: fellowshipContent, loading: false})
    })
  }

  render() {
    if (this.state.loading) {
      return <div style={{width: "100vw", height: "100vh"}}>
               <Loading />
             </div> 
    }

    return (
      <div>
        <Container fluid>
          <Row className="section">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["Fellowship-Header-1"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["Fellowship-Description-1"]}</p>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
          <Row className="section pt-0">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["Fellowship-Header-2"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["Fellowship-Description-2"]}</p>
              <div className="text-right">
                <Button className="uppercase mrs-eaves bold" variant="outline-dark" onClick={() => $('#join-modal-overlay').toggleClass('d-none')}>{this.state.content["Fellowship-Description-2-Button-Text"]}</Button>
              </div>
            </Col>
            <Col xs={0} sm={2} />
          </Row>

          <Row className="section pt-0 bg-color-red fellowship-class-row text-white">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <div className="fellowship-class-image-header">
                <h3 className="mrs-eaves bold header color-red">Fellowship Classes</h3>
                <Image fluid  src={this.state.content["Fellowship-Classes-Image"]}/>
              </div>

              {this.state.content['Fellowship-Classes'].map((fellowshipClass) => { 
                return (<div>
                          <h3 className="mrs-eaves bold header small mt-4 mb-0">{fellowshipClass.title}</h3>
                          <p className="mr-eaves paragraph">{fellowshipClass.names}</p>
                        </div>
                        ) 
                      })
              }

              
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
      </div>
    )
  }
}