import React from 'react'
import { Image } from 'react-bootstrap'
import { Media } from 'react-breakpoints'

export default function MemberCard(props) {


  return (
    <Media>
      {({ breakpoints, currentBreakpoint }) =>
        breakpoints[currentBreakpoint] >= breakpoints.tablet ? (
          <div className="w-50 member-card mb-5 px-2">
            {props.member.image[0] && <Image className="member-image" roundedCircle src={props.member.image[0]} />}
            <div className="ml-2">
              <h3 className="mrs-eaves bold header">{props.member.name}</h3>
              <p className="mr-eaves m-0">{props.member.description}</p>
            </div>
          </div>
        ) : (
          <div className="member-card mb-5 px-2">
            {props.member.image[0] && <Image className="member-image" roundedCircle src={props.member.image[0]} />}
            <div className="ml-2">
              <h3 className="mrs-eaves bold header">{props.member.name}</h3>
              <p className="mr-eaves m-0">{props.member.description}</p>
            </div>
          </div>
        )}
    </Media>
  )
}
