import React, { Component } from 'react'
import { Media } from 'react-breakpoints'
import { Calendar, Views, momentLocalizer } from 'react-big-calendar'
import moment from 'moment'

import 'react-big-calendar/lib/css/react-big-calendar.css'
import '../css/custom-calendar.css'

function Event({ event }) {
  return (
    <div className="event-span">
      <p className="small m-0 paragraph event-title"><strong>{event.title}</strong> - {event.start.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}</p>
    </div>
  )
}

function EventAgenda({ event }) {
  return (
    <div className="">
      <p className="small m-0 paragraph"><strong>{event.title}:</strong></p>
      <p className="small m-0 paragraph">{event.description && event.description}</p>
    </div>
  )
}

export default class MeetingCalendar extends Component {
  render() {
    return (
        <Media>
          {({ breakpoints, currentBreakpoint }) =>
          breakpoints[currentBreakpoint] >= breakpoints.tablet ? (
            <div style={{minHeight: "80vh", height: "100%"}}>
              <Calendar
                popup
                className="mr-eaves"
                events={this.props.events}
                views={[Views.MONTH]}
                showMultiDayTimes
                defaultDate={new Date()}
                view={Views.MONTH}
                localizer={momentLocalizer(moment)}
                components={{
                  event: Event,
                  agenda: {
                    event: EventAgenda
                  }
                }}
              />
            </div>
          ) : (
            <div>
              <Calendar
                popup
                className="mr-eaves"
                events={this.props.events}
                views={[Views.AGENDA]}
                showMultiDayTimes
                defaultDate={new Date()}
                view={Views.AGENDA}
                localizer={momentLocalizer(moment)}
                components={{
                  event: Event,
                  agenda: {
                    event: EventAgenda
                  }
                }}
              />
            </div>
          )}

        </Media>

    )
  }
}
