import React, { Component } from 'react'
import { Button, Col, Container, Image, Row } from 'react-bootstrap'
import { Media } from 'react-breakpoints';
import { Link } from 'react-router-dom';
import Loading from '../../shared-components/Loading';
import $ from 'jquery'

import '../../css/home.css'

export default class HomePage extends Component {

  constructor(props) {
    super(props);

    this.state = {
      loading: true,
      content: null
    }
  }

  componentDidMount() {
    fetch('https://ndohertydesign-api.herokuapp.com/notion-content/nuea/home')
    .then(response => response.json())
    .then((homeContent) => {
      this.setState({content: homeContent, loading: false})
    })
  }

  renderHeader() {
    return (
      <Media>
        {({ breakpoints, currentBreakpoint }) =>
          breakpoints[currentBreakpoint] >= breakpoints.tablet ? (
            <div style={{position: "relative"}}>
              <Image src={this.state.content["Home-Header-Image"]} fluid/>
              <div id="header-overlay">
                <div className="w-75">
                  <h3 className="mrs-eaves bold header">{this.state.content["Home-Main-Header"]}</h3>
                  <p className="mr-eaves paragraph">{this.state.content["Home-Main-Description"]}</p>
                  <Link to="/about">
                    <Button className="uppercase mrs-eaves bold" variant="outline-dark">{this.state.content["Home-Main-Button-Text"]}</Button>
                  </Link>
                </div>
              </div>
            </div>
          ) : (
            <div>
              <Image src={this.state.content["Home-Header-Image"]} fluid/>
              <Container fluid >
                <Row className="section">
                  <Col xs={0} sm={2} />
                  <Col xs={12} sm={8}>
                    <h3 className="mrs-eaves bold header color-red">{this.state.content["Home-Main-Header"]}</h3>
                    <p className="mr-eaves paragraph">{this.state.content["Home-Main-Description"]}</p>
                    <div className="text-right">
                      <Link to="/about">
                        <Button className="uppercase mrs-eaves bold" variant="outline-dark">{this.state.content["Home-Main-Button-Text"]}</Button>
                      </Link>
                    </div>
                  </Col>
                  <Col xs={0} sm={2} />
                </Row>
              </Container>
            </div>
          )
        }
      </Media>
    )
  }

  render() {

    if (this.state.loading) {
      return <div style={{width: "100vw", height: "100vh"}}>
               <Loading />
             </div> 
    }

    return (
      <div>
        { this.renderHeader() }
        <Container fluid className="bg-color-red">
          <Row className="section text-white">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold text-center header mb-5">{this.state.content["Effective-Altruism-Header"]}</h3>
              <h3 className="mrs-eaves bold mt-4 header">{this.state.content["Effective-Altruism-Definition-Header"]}</h3>
              <p className="mr-eaves mb-4 paragraph">{this.state.content["Effective-Altruism-Definition"]}</p>

              <p className="mr-eaves paragraph">{this.state.content["Effective-Altruism-Definition-Description"]}</p>
              <div className="text-right">
                <Link to="/resources">
                  <Button className="uppercase mrs-eaves bold" variant="outline-light">{this.state.content["Effective-Altruism-Definition-Button-Text"]}</Button>
                </Link>
              </div>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
        <Container fluid>
          <Row className="section">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["Home-Text-Header-1"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["Home-Text-Description-1"]}</p>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
          <Row className="section pt-0">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["Home-Text-Header-2"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["Home-Text-Description-2"]}</p>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
          <Row className="section pt-0">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["Home-CTA-Header"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["Home-CTA-Description"]}</p>
              <div className="text-right">
                <Link to="/fellowship">
                  <Button className="uppercase mrs-eaves bold mr-2" variant="outline-dark">{this.state.content["Home-CTA-Button-1-Text"]}</Button>
                </Link>
                <Button className="uppercase mrs-eaves bold" variant="outline-dark" onClick={() => $('#join-modal-overlay').toggleClass('d-none')}>{this.state.content["Home-CTA-Button-2-Text"]}</Button>
              </div>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
      </div>
    )
  }
}
