import React, { Component } from 'react'
import { Container, Row, Col } from 'react-bootstrap'

import MeetingCalendar from '../../shared-components/MeetingCalendar';
import Loading from '../../shared-components/Loading';

import '../../css/resources.css'
export default class ResourcesPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: true,
      content: null
    }
  }

  componentDidMount() {
    fetch('https://ndohertydesign-api.herokuapp.com/notion-content/nuea/resources')
    .then(response => response.json())
    .then((resourcesContent) => {
      this.setState({content: resourcesContent, loading: false})
    })
  }

  formattedEventResources() {
    let formatted = []

    this.state.content["Event-Resources"].forEach((event) => {
      let formattedEvent = {}

      formattedEvent.title = event.name;
      formattedEvent.description = event.description;

      let startDate = new Date(Date.parse(event.date.start))
      formattedEvent.start = startDate

      if (event.date.end) {
        let endDate = new Date(Date.parse(event.date.end))
        formattedEvent.end = endDate
      } else {
        formattedEvent.end = startDate.setSeconds(startDate.getSeconds() + 1)
      }

      formatted.push(formattedEvent)
    })

    return formatted;
  }

  render() {
    if (this.state.loading) {
      return <div style={{width: "100vw", height: "100vh"}}>
               <Loading />
             </div> 
    }


    return (
      <div>
        <Container fluid>
          <Row className="section">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["Calendar-Header"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["Calendar-Description"]}</p>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
          <Row className="section pt-0">
            <Col xs={0} sm={2} />
            <Col  xs={12} sm={8}>
              <MeetingCalendar events={this.formattedEventResources()} />
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
        <Container fluid>
          <Row className="section">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["Resources-Header"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["Resources-Description"]}</p>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
        <Container fluid className="bg-color-red">
          <Row className="section text-white">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              {this.state.content['Resources'].map((resource) => {
                return (
                  <div className="py-4">
                    <h3 className="mrs-eaves bold header">{resource.title}</h3>
                    <a className="text-white" target="_blank" href={resource.url}><p className="mr-eaves italic">{resource.url}</p></a>
                    <p className="mr-eaves paragraph">{resource.description}</p>
                  </div>
                )
              })}
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
      </div>
    )
  }
}
