import React, { Component } from 'react'
import { Col, Container, Row } from 'react-bootstrap'
import ReactContactForm from 'react-mail-form'
import $ from 'jquery'

import Loading from '../../shared-components/Loading';

import '../../css/contact.css'

export default class ContactPage extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: true,
      content: null,
      sent: false
    }
  }

  componentDidMount() {
    fetch('https://ndohertydesign-api.herokuapp.com/notion-content/nuea/contact')
    .then(response => response.json())
    .then((contactContent) => {
      this.setState({content: contactContent, loading: false})
      $('.contact-form a').on('click', () => { this.setState({sent: true}) })
    })
  }

  render() {
    if (this.state.loading) {
      return <div style={{width: "100vw", height: "100vh"}}>
               <Loading />
             </div> 
    }

    if (this.state.sent) {
      return (
        <Container>
          <Row className="section">
            <Col xs={0} sm={2} />
            <Col xs={12} sm={8}>
              <h3 className="mrs-eaves bold header color-red">{this.state.content["Contact-Sent-Header"]}</h3>
              <p className="mr-eaves paragraph">{this.state.content["Contact-Sent-Description"]}</p>
            </Col>
            <Col xs={0} sm={2} />
          </Row>
        </Container>
      )
    }

    return (
      <Container>
        <Row className="section">
          <Col xs={0} sm={2} />
          <Col xs={12} sm={8}>
            <h3 className="mrs-eaves bold header color-red">{this.state.content["Contact-Header"]}</h3>
            <p className="mr-eaves paragraph">{this.state.content["Contact-Description"]}</p>
            <ReactContactForm className="contact-form mr-eaves" to="northeasternea@gmail.com" buttonText="Send" titlePlaceholder="Subject..." contentsPlaceholder="Message..." />
          </Col>
          <Col xs={0} sm={2} />
        </Row>
      </Container>
    )
  }
}
